import React,{Component}from 'react';
import Buttons from '../components/Buttons';
import List from '../components/List';

class Home extends Component{
    constructor(props){
      super(props);
      this.state = {
        data:[1,2,3]
      }
      // setTimeout(()=>{
      //   this.setState({data:[5,6,7,8,9]})
      // },500)
      // 1
      // // fetch('https://cnodejs.org/api/v1/topics')
      // // .then(res=>res.json())
      // // .then(data=>{
      // //   console.log(data);
      // //   this.setState({data:data.data})
      // // })
      // // console.log('didmount');
    }
    componentDidMount(){
      fetch('https://cnodejs.org/api/v1/topics')
      .then(res=>res.json())
      .then(({data})=>{
        // console.log(data);
        this.setState({data})
      })
    }
    getData = (page)=>{
      fetch('https://cnodejs.org/api/v1/topics?page='+ page)
      .then(res=>res.json())
      .then(({data})=>{
        // console.log(data);
        this.setState({data})
      })
    //   console.log(page);
    }
  
  
    render(){
    //   console.log(this.props)
      
      const { data } = this.state;
      return <div>
          <Buttons 
          getData = {this.getData}
          />
          <List data = {data}/>
      </div>
      // return <ul>
      // {
      //   data.map(
      //     (item)=><li>{item.title}</li>
      //   )
      // }
      // </ul>
    }
  }
  
  // console.log($);
  



//命名导出
//默认导出(当前这个文件只能导出一个)
// export default Home;

export default Home;