import React from 'react';


const Buttons = (
    props
    )=>{
    const butns = [1,2,3,4,5]
    return <div>{
        butns.map(
            (item)=>(
                <button 
                key={item}
                onClick={()=>props.getData(item)}
                >
                    {item}
                </button>
            )
        )
        }
    </div>
}


export default Buttons;